var TokenKey = 'token';
function getToken() {
  return localStorage.getItem(TokenKey);
}
function setToken(token) {
  localStorage.setItem(TokenKey, token);
}
function removeToken() {
  localStorage.removeItem(TokenKey);
}

var limited = 'x-auth-limited';
function getLimited() {
  return localStorage.getItem(limited);
}
function setLimited(data) {
  localStorage.setItem(limited, data);
}

var limitedUrl = 'x-auth-limitedUrl';
function getLimitedUrl() {
  return localStorage.getItem(limitedUrl);
}
function setLimitedUrl(data) {
  localStorage.setItem(limitedUrl, data);
}
